#display{
    border: 0px solid black;
    width:80%;
    display: grid;
    grid-template-columns: repeat(4,200px);
    /* grid-template-rows: repeat(4,100px); */
    margin: auto;
    gap:10px;
   
}
.box{
border: 0px solid rgb(223, 32, 213);
justify-content: center;
text-align: center;
padding: 5px;
width:280px;
height: 100%;
margin-top: 10px;
margin: auto;
box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;


 }
 
 .boxes{
   border: 0px solid rgb(238, 232, 238);
   
   justify-content: center;
   text-align: center;
   padding: 5px;
   width:80%;
   height: 100%;
   margin-top: 10px;
   margin: auto;
  border-radius: 5px;
   box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
   
   
    }

 .boxes>img{
    margin: auto;
    border: 0px solid red;
    height: 70%;
 }

 .tool{
   border: 0px solid green;
   justify-content: center;
   text-align: center;
   padding: 5px;
   width:80%;
   height: 100%;
   margin-top: 10px;
   margin: auto;
   box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
   
   
    }
    
 .tool>img{
   margin: auto;
   border: 0px solid red;
   height: 70%;
  
}

